@import "../../Style/mixins.scss";
@import "../../Style/variables.scss";

.product_container {
  display: flex;
  gap: 20px;
  margin: 10px auto;
  width: 98%;
  height: 100%;
  flex: 1 0 auto;
}
.btns {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.informers {
  position: absolute;
  top: 10px;
  right: 100px;
  padding-right: 60px;

  display: flex;
  gap: 10px;
}
.product_left {
  width: 500px;
  display: flex;
  align-items: center;
  gap: 10px;
  flex-direction: column;
}

.product_right {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  padding: 10px 10px;
  background-color: white;
  border-radius: 10px;
  box-shadow: $boxshadow_big;
  flex: 1;
}
@media only screen and (max-width: 980px) {
  .product_container {
    flex-direction: column;
    .product_left {
      width: 100%;
    }
    .btns {
      gap: 5px;
      position: fixed;
      bottom: 20px;
      z-index: 10;
      background-color: rgba($color: #bc7503, $alpha: 0.4);
      height: 50px;
      align-items: center;
      button {
        background-color: $color_darken_orange;
      }
    }
  }
}
@media only screen and (max-width: 500px) {
  .informers {
    position: relative;
    top: 0;
    left: 0;
    margin-bottom: 30px;
  }
}
